<template>
  <el-container class="detail-form-container">
    <el-header><h1>项目具体信息</h1></el-header>
    <el-form
      :model="project"
      :rules="rules"
      ref="project"
      label-width="120px"
      class="detail-form"
    >
      <el-form-item label="项目编号" prop="id">
        <el-input :disabled="true" v-model="project.id"></el-input>
      </el-form-item>
      <el-form-item label="项目名称" prop="name">
        <el-input v-model="project.name"></el-input>
      </el-form-item>
      <el-form-item label="项目类别" prop="category">
        <el-radio-group v-model="project.category">
          <el-radio :label="0">我提我做</el-radio>
          <el-radio :label="1">我提你做</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="项目描述" prop="description">
        <el-input
          type="textarea"
          :rows="4"
          placeholder="请输入内容"
          v-model="project.description"
        >
        </el-input>
      </el-form-item>
      <el-form-item label="预期成果及收益" prop="expectation">
        <el-input
          type="textarea"
          :rows="4"
          placeholder="请输入内容"
          v-model="project.expectation"
        >
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="back()">返回</el-button>
        <el-button type="primary" @click="submitForm('project')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      project: {
        id: 0,
        name: "",
        category: 0,
        description: "",
        expectation: "",
      },
      rules: {
        name: [{ required: true, message: "请输入项目名称", trigger: "blur" }],
        description: [
          { required: true, message: "请输入项目描述", trigger: "blur" },
        ],
        expectation: [
          { required: true, message: "请输入内容", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    back() {
      this.$router.push("/");
    },
    submitForm(formName) {
      const id = this.$route.params.id;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http
            .put(`http://localhost:3000/projects/${id}`, this.project, {
              headers: {
                "Content-Type": "application/json",
              },
            })
            .then(() => {
              this.$router.push("/");
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  async created() {
    const id = this.$route.params.id;
    const response = await this.$http.get(
      `http://localhost:3000/projects/${id}`
    );
    this.project = { ...this.project, ...response.data };
  },
};
</script>