<template>
  <el-container>
    <el-header>
      <h1>创新招募信息展示板</h1>
      <el-row class="search-container">
        <label>项目名称: </label>
        <el-input
          class="search-input"
          label="项目名称"
          v-model="searchKey"
          placeholder="请输入搜索关键字"
        />
        <el-button type="primary" round @click="search">搜索</el-button>
      </el-row>
      <el-main>
        <el-table
          :stripe="true"
          :border="true"
          :data="projects"
          style="width: 100%"
        >
          <el-table-column label="项目编号" prop="id"></el-table-column>
          <el-table-column label="项目名称" prop="name"></el-table-column>
          <el-table-column label="提交人" prop="submitter"></el-table-column>
          <el-table-column label="部门" prop="department"></el-table-column>
          <el-table-column
            label="项目描述"
            prop="description"
          ></el-table-column>
          <el-table-column
            label="预期成果及收益"
            prop="expectation"
          ></el-table-column>
          <el-table-column label="指派给" prop="assignee"></el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="openDetailPage(scope.row)"
                >我有方案</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="deleteItem(scope.row)"
                >点击删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <el-pagination
          @prev-click="handlePageClick"
          @next-click="handlePageClick"
          @current-change="handlePageClick"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="total"
        />
      </el-footer>
    </el-header>
  </el-container>
</template>


<script>
export default {
  name: "DisplayTable",
  inject: ["reload"],
  data() {
    return {
      projects: [],
      searchKey: "",
      replicas: [],
      total: 0,
    };
  },
  methods: {
    search: function () {
      if (this.searchKey.trim().length === 0) {
        this.projects = this.replicas;
      } else {
        this.projects = this.projects.filter(
          (item) => item.name.indexOf(this.searchKey) > -1
        );
      }
    },
    async handlePageClick(page) {
      const response = await this.$http.get(
        `http://localhost:3000/projects?_page=${page}&_limit=10`
      );
      this.projects = response.data;
    },
    openDetailPage(row) {
      this.$router.push({
        path: `/projects/${row.id}`,
        params: { id: row.id },
      });
    },
    async deleteItem(row) {
      const confirmResult = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除",
        });
      });

      if (confirmResult !== "confirm") {
        return this.$message.info("已取消删除");
      }

      const { data: res } = await this.$http.delete(
        `http://localhost:3000/projects/${row.id}`
      );
      if (res.meta.status !== 200) {
        return this.$message.error("删除用户失败！");
      }
      this.$message.success("删除用户成功！");
      this.reload();
    },
  },
  async created() {
    const response = await this.$http.get("http://localhost:3000/projects");
    // this.projects = response.data;
    const data = response.data;
    this.total = data.length;
    this.projects = data.slice(0, 10);
    this.replicas = data.slice(0, 10);
  },
};
</script>


<style scoped>
.search-container {
  text-align: left;
  margin-left: 20px;
}
.search-container > div {
  width: 400px;
  margin-right: 20px;
}
</style>